import { Component, OnInit } from '@angular/core';
import { TodoService } from '../todo.service';
import { iToDo } from '../to-do';

@Component({
  selector: 'app-todo-list',
  templateUrl: './todo-list.component.html',
  styleUrls: ['./todo-list.component.css']
})
export class TodoListComponent implements OnInit {

  private todos: iToDo[] = [ 
    {
      id: 1,
      text: 'test one'
    },
    {
      id: 2,
      text: 'test two'
    }
  ];

  constructor(private todoService: TodoService) {}

  ngOnInit() {
    console.log('HttpDemoComponent::ngOnInit()');

    this.todoService.getToDos().subscribe(
      data => {
        console.log(data);
        this.todos = data; 
      }
    );
  }

  public addNewItem(newItemText: string) {
    // Create an iToDo object. Set the id to 0 as this will be set on
    // the server to a correct value
    let todo:iToDo = {id: 0, text: newItemText};

    this.todoService.addToDo(todo).subscribe(
      allItems => { this.todos =  allItems; }
    );
  }

  public deleteItem(itemID: number):Boolean {
    this.todoService.deleteToDo(itemID).subscribe(
      data => {
        console.log('Here is what I got back from deleting ' + data);
        this.todos = data; 
      }
    );

    // I always return false as this is called from an anchor click handler
    // and returning false will ensure the page does not refresh.
    return false;
  }

}
