import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { iToDo } from './to-do';

@Injectable({
  providedIn: 'root'
})
export class TodoService {
  // You can remix this glitch to see the code
  private webURI: string = 'https://cultured-macaroni.glitch.me/';

  private httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
  };

  constructor(private http: HttpClient) { 
    console.log('HttpDemoService::constructor()');
  }

  public getToDos(): Observable<iToDo[]>  {
    return this.http.get<iToDo[]>(this.webURI.concat('todos'));
  }

  public addToDo(newItem: iToDo): Observable<iToDo[]> {

    return this.http.post<iToDo[]>(this.webURI.concat('todo'), {todo: newItem}, this.httpOptions);
  }

  public deleteToDo(itemID: number): Observable<iToDo[]>  {
    return this.http.post<iToDo[]>(this.webURI.concat('deleteToDos'), {id: itemID}, this.httpOptions);
  }

}

